@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.schedule {
  background: #2b3769;
  padding: 13rem 50px;
}
@media screen and (max-width: 1440px) {
  .schedule {
    padding: 13rem 30px;
  }
}
@media screen and (max-width: 690px) {
  .schedule {
    padding: 50px 30px;
  }
}
@media screen and (max-width: 375px) {
  .schedule {
    padding: 50px 10px;
  }
}
.schedule__body {
  position: relative;
}
.schedule__row {
  position: relative;
  z-index: 9;
}
.schedule__items {
  display: grid;
  height: 100%;
  gap: 60px;
  justify-content: space-between;
  grid-template-columns: repeat(3, minmax(200px, 1fr));
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .schedule__items {
    gap: calc(20px + 40 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 1440px) {
  .schedule__items {
    grid-template-columns: repeat(2, minmax(200px, 1fr));
    gap: 20px;
  }
}
@media screen and (max-width: 690px) {
  .schedule__items {
    grid-template-columns: repeat(1, minmax(200px, 1fr));
  }
}
.schedule__item {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.schedule__item h2 {
  color: #ffffff;
  font-size: 65px;
  font-weight: 400;
  line-height: 100%;
}
@media (min-width: 690.98px) {
  .schedule__item h2 {
    max-width: 300px;
  }
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .schedule__item h2 {
    font-size: calc(28px + 37 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .schedule__item h2 {
    font-size: 28px;
  }
}
@media screen and (max-width: 690px) {
  .schedule__item h2 {
    text-align: center;
  }
}
.schedule__title {
  margin-bottom: 30px;
  color: #ffffff;
}
@media screen and (max-width: 768px) {
  .schedule__title {
    white-space: normal;
  }
}
@media screen and (max-width: 690px) {
  .schedule__title {
    margin-bottom: 10px;
  }
}
.schedule__number {
  margin-bottom: 7px;
  padding-bottom: 20px;
  color: #ffffff;
  font-size: 96px;
  font-weight: 400;
  line-height: 123.958333%; /* 119px/96 */
  border-bottom: 1px solid #a9a9a9;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .schedule__number {
    font-size: calc(40px + 56 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .schedule__number {
    font-size: 40px;
  }
}
.schedule__text {
  color: #ffffff;
  font-family: "averta-regular", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 125%; /* 20px/16 */
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .schedule__text {
    font-size: calc(12px + 4 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .schedule__text {
    font-size: 12px;
  }
}